import {
  Box,
  IconButton,
  useTheme,
  InputBase,
  Tooltip, Link, DropDown
} from "@mui/material";
import csvData from "../../SearchData/nasdaq-listed-filtered.json"
import { SketchPicker } from "react-color";
import ClickAwayListener from "@mui/material/ClickAwayListener";
import { useContext, useState, useRef} from "react";
import { ColorModeContext, tokens } from "../../theme";
import LightModeOutlinedIcon from "@mui/icons-material/LightMode";
import DarkModeOutlinedIcon from "@mui/icons-material/DarkMode";
import NotificationsOutlinedIcon from "@mui/icons-material/NotificationsOutlined";
import SettingsOutlinedIcon from "@mui/icons-material/SettingsOutlined";
import PersonOutlinedIcon from "@mui/icons-material/PersonOutlined";
import SearchIcon from "@mui/icons-material/Search";

const Topbar = ({ searchValue, setSearchValue, lineColor, setLineColor}) => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const colorMode = useContext(ColorModeContext);
  const [colorPickerVisible, setColorPickerVisible] = useState(false);

  const handleClickAway = () => {
    setColorPickerVisible(false);
  };

  const [suggestions, setSuggestions] = useState([]);
  const searchInputRef = useRef(null);
  
  const suggestCompanies = (query) =>{
    if(!query){
      setSuggestions([]);
      return;
    }
    const matched = csvData.filter(item =>
      item['Company Name'].toLowerCase().includes(query.toLowerCase())).slice(0,5)
      setSuggestions(matched)
  }

  // Search Value is taken
  const [searchInput, setSearchInput] = useState("");
  const onSearch = (value) => {
    setSearchValue(value);
};
   const handleSearch = (event) => {
    event.preventDefault();
    const company = csvData.find(item => item['Company Name'] === searchInput);
      if (company) {
        console.log("Selected company symbol:", company.Symbol);
        setSearchValue(company.Symbol);
      } else {
        console.log("Company not found.");
      }
    setSuggestions([]);
    setSearchInput("");
  };





  return (
    <Box display={"flex"} justifyContent={"space-between"} p={2}>
      {/* SEARCH BAR */}
      <form onSubmit={handleSearch}>
        <Box display={"flex"} backgroundColor={colors.primary[400]} borderRadius="3px">
            <InputBase
              ref={searchInputRef}
              sx={{ ml: 2, flex: 1 }}
              placeholder={"Search"}
              value={searchInput}
              onChange={(e) => {
                setSearchInput(e.target.value);
                suggestCompanies(e.target.value);
              }}
            />
          
          <IconButton type="submit" sx={{ p: 1 }}>
            <SearchIcon />
          </IconButton>
        </Box>
        {suggestions.length > 0 && (
          <Box border="1px solid #ccc" borderRadius="4px" mt={1} position={"absolute"}>
            {suggestions.map((suggestion, index) => (
              <Box
                key={index}
                
                cursor ="pointer"
                onClick={() => {
                  setSearchInput(suggestion['Company Name']);
                  setSuggestions([]);
                }}
                >
                  {suggestion['Company Name']}
                  </Box>
            ))}
            </Box>
        )}
      </form>

      {/* ICONS */}
      <Box display={"flex"}>
        <IconButton onClick={colorMode.toggleColorMode}>
          {theme.palette.mode === "dark" ? (
            <DarkModeOutlinedIcon />
          ) : (
            <LightModeOutlinedIcon />
          )}
        </IconButton>
        <Tooltip title="You have no new notifications">
          <IconButton>
            <NotificationsOutlinedIcon />
          </IconButton>
        </Tooltip>
        <ClickAwayListener onClickAway={handleClickAway}>
          <Box>
            <IconButton onClick={() => setColorPickerVisible(!colorPickerVisible)}>
              <SettingsOutlinedIcon />
            </IconButton>
            {colorPickerVisible && (
              <Box zIndex={10} position="absolute">
                <SketchPicker 

                  color={lineColor}
                  onChangeComplete={(color) => {
                    setLineColor(color.hex);
                  }}
                />
              </Box>
            )}
          </Box>
        </ClickAwayListener>
        <Tooltip title="GitHub Page">
          <Link href="https://github.com/lwilliams002" target="_blank" rel="noopener noreferrer">
          <IconButton>
            <PersonOutlinedIcon />
          </IconButton>
        </Link>
        </Tooltip>


      </Box>
    </Box>
  );
};

export default Topbar;